.footer{background:#323232;display:flex;justify-content:center;align-items:center;height:3em}.footer h3{color:#14ffec;font-size:1.25em;font-weight:300}@media screen and (max-width: 425px){.footer h3{font-size:.8em}}@media screen and (max-width: 330px){.footer h3{font-size:.7em}}.menu{height:50px;left:1em;top:1em;z-index:2;display:none;color:#212121;fill:currentColor;position:fixed}@media screen and (max-width: 767px){.menu{display:block}}.barra_navegación-principal{width:38%;padding:0 0 0 6.3em}.navBar_container-principal,.navBar_container{display:flex;align-items:center;height:4em}.navBar_container{background:aqua}.lista_navegacion-principal{display:flex;width:100%;justify-content:space-between;margin:0;padding:0}.lista_navegacion-principal li{list-style:none;font-weight:600}.lista_navegacion-principal li a{text-decoration:none;color:#323232}@media screen and (max-width: 768px){.barra_navegación-principal{width:45%;padding:0 0 0 3em}}@media screen and (max-width: 767px){.lista_navegacion-principal{position:fixed;width:100vw;height:100vh;align-items:center;justify-content:center;transition:.3s;flex-direction:column;top:0;bottom:0;left:-100vw;background-color:wheat;z-index:1}.lista_navegacion-principal.isActive{left:0}.lista_navegacion-principal li{margin:1em 0}}.separadorSection{position:relative}.custom-shape-divider,.custom-shape-divider-bottom{position:absolute;top:0;left:0;width:100%;overflow:hidden;line-height:0}.custom-shape-divider-bottom{top:unset;bottom:0;transform:rotate(180deg)}.custom-shape-divider svg,.custom-shape-divider-bottom svg{position:relative;display:block;width:calc(100% + 1.3px)}.custom-shape-divider-bottom svg{position:relative;display:block;width:calc(100% + 1.3px)}*{font-family:"Raleway",sans-serif}body{padding:0;margin:0;scroll-behavior:smooth}.resalt{display:inline}